@import url(https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Pirata+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bungee+Inline&family=Pirata+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  color: #151515;
  font-family: "roboto";
  padding-left: 20%;
  padding-right: 20%;
  padding-top: 5%;
  background-image: url(/Resume/static/media/flowers.97cb698e.jpeg);
  background-size: cover;
}
.container-border {
  border: solid 1rem #a9fdac;
}

.resume-section,
.home-page {
  text-align: center;
  background-color: #f1ffe7;
  /* border: solid 1rem #a9fdac; */
}
.skill-section {
  list-style: none;
  list-style-type: none;
}

.accordion-flush .accordion-button {
  display: inline-block;
  background-color: #f1ffe7;
  /* background-color: #55DBCB; */
  color: #151515;
}
h1 {
  font-family: "Bungee Inline";
  font-size: 2.5rem;
}
.view-resume {
  font-size: 2.5rem;
}
h3 {
  text-decoration: underline;
  font-family: "Bungee Inline";
  font-size: 2rem;
}
.education,
.experience,
.skills {
  font-size: 1.09rem;
}

.view-resume {
  text-align: center;
  color: #151515;
  padding: 0%;
  margin: 0%;
}
.accordion-header {
  padding-top: 1rem;
}

.basics-section {
  color: #151515;
  font-family: "Bungee Inline";
  background-color: #f1ffe7;
}
/* border: solid 1rem #a9fdac; */

.basics-name {
  font-size: 3rem;
}
.basic-info {
  text-decoration: none;
}
.summary-section {
  margin-left: 8rem;
  margin-right: 8rem;
  padding-bottom: 0.5rem;
  font-family: "roboto";
}
.pure-button {
  text-decoration: none;
  font-size: 2rem;
  color: #151515;
}
.pure-button:hover {
  color: black;
}
li {
  list-style-type: none;
  font-weight: bold;
  font-family: "roboto";
}

